import React from 'react';
import PropTypes from 'prop-types';
import GaugeChart from 'react-gauge-chart'

SensorValue.propTypes = {
    values: PropTypes.array,
};

SensorValue.defaultProps = {
    values: [],
}


function SensorValue(props) {
    const { values } = props;
    
    return (
        <div>
            {values.map(value => (
                <div style={{ textAlign: 'center' }}>
                <h2 className="page-header">Real-time Data</h2>
                <div className="row">
                    <div className="col-6">
                        <h4>Temperature</h4>
                        <GaugeChart id="gauge-chart4"
                            nrOfLevels={10}
                            arcPadding={0.1}
                            cornerRadius={3}
                            percent={value.field1/100}
                            formatTextValue={value => value + '\u00b0C'}
                            textColor='black'
                        />
                    </div>
                    <div className="col-6">
                        <h4>Humidity</h4>
                        <GaugeChart id="gauge-chart4"
                            nrOfLevels={10}
                            arcPadding={0.1}
                            cornerRadius={3}
                            percent={value.field2/100}
                            textColor='black'
                        />
                    </div>
                </div>
                <br />
                <div className="row">
                    <div className="col-6">
                        <h4 style={{ textAlign: 'center' }}>Air Qualitty Index</h4>
                        <GaugeChart id="gauge-chart4"
                            nrOfLevels={10}
                            arcPadding={0.1}
                            cornerRadius={3}
                            percent={value.field3/100}
                            formatTextValue={value => value + 'ppm'}
                            textColor='black'
                        />
                    </div>
                    <div className="col-6">
                        <h4 style={{ textAlign: 'center' }}>Noise Level</h4>
                        <GaugeChart id="gauge-chart4"
                            nrOfLevels={10}
                            arcPadding={0.1}
                            cornerRadius={3}
                            percent={value.field4/100}
                            formatTextValue={value => value + 'dB'}
                            textColor='black'
                        />
                    </div>
                </div>
            </div>

            ))}

        </div>
    );
}

export default
    SensorValue;